import { 
  SET_SEARCH, 
  SET_BANNER,
  SET_PERSONAL
 } from './actionType'
import * as api from '@/api'
export const searchFoucs = (flag) => ({
  type: SET_SEARCH,
  value: flag
});
export const setBanner = (res) => ({
  type: SET_BANNER,
  value: res
});
export const setPersonal = (res) => ({
  type: SET_PERSONAL,
  value: res
});
export const getList = (flag) => {
  // return 
  return (dispatch) => {
    // 
    let action  = searchFoucs(flag)
    // dispatch
    dispatch(action)
  }
}
export const getBaner = () => {
  // return 
  return (dispatch) => {
    // 
    api.getSongUr({}).then(res => {
      let action  = setBanner(res.banners || [])
      dispatch(action)
    }).catch(err => {
      console.log(err)
    })
    // dispatch
    
  }
}
export const getPersonal = () => {
  // return 
  return (dispatch) => {
    // 
    api.getPersonal({}).then(res => {
      let action  = setPersonal(res.result || [])
      dispatch(action)
    }).catch(err => {
      console.log(err)
    })
    // dispatch
    
  }
}